.title {
    font-weight: 800;
}

.size_type_big {
    font-size: 30px;
    line-height: 1.2;
}

.size_type_normal {
    font-size: 24px;
    line-height: 120%;
}

.size_type_small {
    font-size: 18px;
    line-height: 120%;
}

.color_type_white {
    color: #FFF;
}

.color_type_black {
    color: #000000;
}

.align_type_center {
    text-align: center;
}

@media screen and (max-width: 600px) {
    .size_type_big {
        font-size: 22px;
    }

    .size_type_normal {
        font-size: 18px;
    }
}