.game-done {
    background:
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    max-width: 670px;
    margin: auto;
    padding: 100px 85px 75px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 30px;
}

.game-done__image {
    margin-bottom: 45px;
}

.game-done__title {
    margin-bottom: 15px;
}

.game-done__text {
    margin-bottom: 20px;
}

.game-done__button {
    max-width: 310px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
}

@media screen and (max-width: 600px) {
    .game-done {
        padding: 60px 25px 15px;
    }

    .game-done__image{
        width: 140px;
    }
}