.header {
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;
}

.header-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login-button {
    display: flex;
    align-items: center;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background-color: var(--blue);
    gap: 5px;
    font-size: 14px;
    transition: .3s;
}

.login-button:hover {
    box-shadow: 0px 4px 30.9px 0px var(--blue);
}

.login-button span {
    color: #fff;
}

.header__back {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    box-shadow: 0px 4px 26px 0px #00000014;
    background-color: #fff;
}

.user-button {
    display: flex;
    align-items: center;
    width: max-content;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    color: var(--blue);
    border: 1px solid var(--blue);
    gap: 5px;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 14px;
    transition: .3s box-shadow;
}

.user-button:hover {
    box-shadow: 0px 4px 30.9px 0px var(--blue);
}


.user-wrapper {
    position: relative;
}

.user-menu {
    position: absolute;
    z-index: 2;
    width: 250px;
    right: 0;
    top: calc(100% + 10px);
    background-color: #fff;
    border: 1px solid #0000000F;
    border-radius: 25px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.user-menu__email {
    margin-bottom: 15px;
}

.user-menu__button {
    border: 1px solid #0000000F;
}

.header__logo {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
}

@media screen and (max-width: 600px) {
    .header {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .login-button {
        width: 40px;
        height: 40px;
    }

    .login-button span {
        display: none;
    }

    .header__logo {
        width: 83px;
        height: 63px;
    }

    .user-button {
        width: 40px;
        height: 40px;
        padding: 0;
    }

    .user-button span {
        display: none;
    }
}