.main {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
}

.main-content {
    margin-top: auto;
    margin-bottom: auto;
}

.game-header {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    width: max-content;
    transform: translateX(30px)
    /* position: relative; */
}

.game-header__pic {
    position: absolute;
    left: -60px;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;
}

@media screen and (max-width: 600px) {
    .game-header__pic {
        width: 70px;
        left: -35px;
    }
}