.main {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
}

.main-content {
    margin-top: auto;
    margin-bottom: auto;
    padding-bottom: 20dvh;
}

.main-days {
    width: 850px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.main-day-wrapper {
    padding: 2px;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 99px;
    border-radius: 50px;
}

.main-day {
    background: linear-gradient(0deg, rgba(68, 21, 0, 0.03), rgba(68, 21, 0, 0.03)),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    height: 95px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    border-radius: 48px;
    gap: 15px;
    width: 100%;
}

.main-day__pic {
    position: relative;
    width: 53px;
    height: 53px;
    display: flex;
    align-items: center;
    border-radius: 50%;
    background-color: var(--blue);
    flex-shrink: 0;
}

.main-day__pic img {
    position: absolute;
    left: -10px;
    /* right: 0; */
    bottom: -5px;
}

.day-info {
    width: 100%;
    height: 55px;
    border-radius: 28px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
    padding-left: 15px;
    /* padding-right: 15px; */
    /* flex-shrink: 0; */
    /* width: 490px; */
    background-color: #00000012;
}

.day-info_active {
    background-color: #fff;
}

.day-info__text span {
    color: #00000099;
}

.day-info__check {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #0000002B;
    border-radius: 15px;
    background-color: #fff;
}

.day-info__check_active {
    background-color: #E73F0C;
    border: 2px solid #0000002B;
}

.main-day__button {
    width: 230px !important;
    flex-shrink: 0;
    margin-left: auto;
}

.day-timer {
    width: 385px;
    height: 55px;
    display: flex;
    align-items: center;
    background: #FFFFFF0F;
    border-radius: 28px;
    margin-left: auto;
    padding-left: 25px;
    padding-right: 25px;
    justify-content: space-between;
}

.day-timer__text {
    margin-right: auto;
    margin-left: 10px;
    font-size: 14px;
    color: #ffffff88;
}

.day-timer__time {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
}

.login-button{
    border: 1px solid #FFFFFF38;
}

.login-button>span{
    display: flex;
    gap: 10px;
}

.to-task-button>span {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.to-task-button__icon {
    background-color: #fff;
    border-radius: 18px;
    height: 35px;
    width: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0px;
    border: 2px solid #F9F4EC;
}

.task-done-button>span {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
}


/* .main-day__button>span {
    gap: 5px;
    display: flex;
    align-items: center;
} */

.main-day__button_locked > span{
    display: flex;
    align-items: center;
    gap: 5px;
}

.main-wrapper {
    display: flex;
    align-items: center;
    gap: 50px;
}

.progress-block {
    position: relative;
    width: 380px;
    background-image: url(../../../public/assets/images/indicator-bg.png);
    background-repeat: no-repeat;
    background-position: center 0;
    background-size: 310px;
    height: 700px;
    display: flex;
    flex-direction: column;
}

.progress-block_day_1 {
    background-image: url(../../../public/assets/images/indicator-day1.png), url(../../../public/assets/images/indicator-bg.png);
    background-size: 480px, 310px;
    background-position: center, center 0;
}

.progress-block_day_2 {
    background-image: url(../../../public/assets/images/indicator-day2.png), url(../../../public/assets/images/indicator-bg.png);
    background-size: 390px, 310px;
    background-position: center, center 0;
}

.progress-block_day_3 {
    background-image: url(../../../public/assets/images/indicator-day3.png), url(../../../public/assets/images/indicator-bg.png);
    background-size: 390px, 310px;
    background-position: center, center 0;
}

.progress-block_day_4 {
    background-image: url(../../../public/assets/images/indicator-day4.png), url(../../../public/assets/images/indicator-bg.png);
    background-size: 390px, 310px;
    background-position: center, center 0;
}

.progress-block_day_5 {
    background-image: url(../../../public/assets/images/indicator-day5.png), url(../../../public/assets/images/indicator-bg.png);
    background-size: 390px, 310px;
    background-position: center, center 0;
}

.progress-block__logo {
    width: 183px;
    height: 140px;
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: auto;
}

.progress-bar {
    display: flex;
    margin-top: auto;
    position: relative;
    width: 330px;
    height: 180px;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.progress-bar__item {
    position: absolute;
    width: 53px;
    height: 53px;
    align-items: center;
    justify-content: center;
    display: flex;
    border: 2.12px solid #F9F4EC;
    border-radius: 27px;
    box-shadow: 0px 4.24px 15.9px 0px #0000000D;
    background-color: #fff;
    /* opacity: .7; */
}

.progress-bar__item img {
    opacity: .4;
}

.progress-bar__item_active {
    opacity: 1;
    background:
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
}

.progress-bar__item_active img {
    opacity: 1;
}

.progress-bar__item_type_chocolate {
    left: 0;
    top: 0;
}

.progress-bar__item_type_nut {
    left: 40px;
    top: 80px;
}

.progress-bar__item_type_waffle {
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.progress-bar__item_type_milk {
    right: 40px;
    top: 80px;
}

.progress-bar__item_type_caramel {
    right: 0;
    top: 0;
}

@media screen and (max-width: 600px) {
    .main-wrapper {
        flex-direction: column-reverse;
        padding-top: 100px;
        gap: 20px;
    }

    .main-days {
        width: calc(100% + 30px);
    }

    .main-days :global(.swiper) {
        width: 100%;
    }

    .main-day {
        flex-direction: column;
        height: 300px;
        padding-left: 12px;
        padding-right: 12px;
        gap: 0;
    }

    .main-day-wrapper {
        height: auto;
        margin-top: 20px;
    }

    .main-day__pic {
        width: 110px;
        height: 110px;
        margin-top: 20px;
        margin-bottom: 35px;
    }

    .main-day__pic img {
        width: 140px;
        height: 140px;
        left: -15px;
    }

    .day-info__check {
        width: 21px;
        height: 21px;
        flex-shrink: 0;
    }

    .day-info {
        height: 45px;
        position: relative;
    }

    .day-info__text {
        font-size: 15px;
    }

    .main-day__button {
        width: 100% !important;
        margin-top: 15px;
    }

    .main-days :global(.swiper-slide) {
        width: 255px;
    }

    .day-timer {
        position: absolute;
        left: 0;
        top: calc(100% + 10px);
        width: 100%;
        background-color: #FFFFFF1A;
        padding-left: 15px;
        padding-right: 15px;
    }

    .day-timer svg {
        width: 20px;
    }

    .progress-block__logo{
        width: 120px;
        height: 93px;
    }

    .progress-block {
        width: 280px;
        height: 500px;
        background-size: 190px;
    }

    .progress-block_day_1 {
        background-size: 300px, 190px;
        background-position: center, center 0;
    }

    .progress-block_day_2 {
        background-size: 250px, 190px;
        background-position: center, center 0;
    }

    .progress-block_day_3 {
        background-size: 250px, 190px;
        background-position: center, center 0;
    }

    .progress-block_day_4 {
        background-size: 250px, 190px;
        background-position: center, center 0;
    }

    .progress-block_day_5 {
        background-size: 250px, 190px;
        background-position: center, center 0;
    }

    .progress-bar {
        width: 220px;
        height: 140px;
    }

    .progress-bar__line svg {
        width: 192px;
    }

    .progress-bar__item {
        width: 40px;
        height: 40px;
    }

    .progress-bar__item img {
        width: 22px;
    }

    .progress-bar__item_type_chocolate {
        top: 15px;
    }

    .progress-bar__item_type_nut {
        left: 30px;
        top: 70px;
    }

    .progress-bar__item_type_waffle {
        left: 0;
        right: 0;
        bottom: 10px;
        margin: auto;
    }

    .progress-bar__item_type_milk {
        right: 30px;
        top: 70px;
    }

    .progress-bar__item_type_caramel {
        right: 0;
        top: 15px;
    }

    .main__title{
        position: absolute;
        top: calc(100%);
        left: 0;
        right: 0;
        margin: auto;
    }
}