.main {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
}

.main-content {
    margin-top: auto;
    margin-bottom: auto;
}

.day-wrapper {
    background-color: #fff;
    padding: 30px;
    border-radius: 80px 80px 0 0;
    padding-bottom: 25dvh;
}

.day-header {
    background: linear-gradient(0deg, rgba(68, 21, 0, 0.03), rgba(68, 21, 0, 0.03)),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    height: 95px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 48px;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 65px;
}

.day-header__day {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    border-radius: 20px;
    box-shadow: 0px 4px 4px 0px #FFFFFF54 inset, 0px 12px 26.7px 0px #F3410E91;
    background: linear-gradient(0deg, #E73F0C, #E73F0C),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%),
        linear-gradient(0deg, #F3410E, #F3410E);
    width: 100px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
}

.day-header__counter {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    border-radius: 20px;
    box-shadow: 0px 4px 15px 0px #0000000D;
    background-color: #fff;
    width: 100px;
    color: #000;
    font-size: 14px;
    font-weight: 500;
}

.day-swiper {
    width: 100%;
}

.day-swiper :global(.swiper-slide) {
    width: 225px;
}

.day-items {
    /* display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px; */
}


.day-item {
    padding: 2px;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    border-radius: 30px;
}

.day-item__content {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #F9F4EC;
    border-radius: 30px;
    align-items: center;
}

.day-item__pic-wrapper {
    position: relative;
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    padding: 3px;
    margin-bottom: 20px;
}

.day-item__pic {
    background: radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 10px solid #fff;
    position: relative;
}

.day-item__pic img {
    position: absolute;
    left: -10px;
    right: 0;
    margin: auto;
    width: 144px;
    height: 144px;
    object-fit: contain;
    object-position: center;
    /* right: 0; */
    /* margin: auto; */
    bottom: 15px;
}

.day-item__title {
    text-align: center;
    margin-bottom: 20px;
}

.day-item__login-button>span {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--blue);
}

.penalty-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
}

.penalty-block__button {
    width: 190px;
    margin-top: 30px;
}

.day-is-done {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    background-image: url(../../../public/assets/images/day-confetti.png);
    background-repeat: no-repeat;
    background-position: center 0;
    background-size: contain;
    margin-left: auto;
    margin-right: auto;
    margin-top: -65px;
    padding-top: 80px;
    padding-bottom: 80px;
}

.day-is-done__pic {
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    box-shadow: 0px 7.98px 112.1px 0px #C3850082;
    width: 140px;
    height: 100px;
    padding: 8px;
    border-radius: 50px;
    display: flex;
    transform: rotate(-9deg);
    margin-bottom: 30px;
}

.day-is-done__pic-content{
    background-color: #fff;
    border-radius: 50px;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    gap: 5px;
}

.day-is-done__button{
    width: 300px;
    margin-top: 40px;
}

@media screen and (max-width: 600px) {
    .day-wrapper {
        padding-left: 25px;
        padding-right: 25px;
        padding-top: 30px;
        border-radius: 40px;
    }

    .day-header__title {
        font-size: 18px;
        margin-bottom: 5px;
    }

    .day-header {
        flex-direction: column;
        height: 65px;
    }

    .day-header__counter {
        margin-bottom: -10px;
        flex-shrink: 0;
        height: 27px;
        width: 65px;
    }

    .day-header__day {
        background: transparent;
        box-shadow: none;
        color: #ffffff88;
        height: 16px;
        width: auto;
        margin-top: 10px;
    }

    .day-items {
        width: calc(100% + 25px);
    }

    .day-is-done{
        padding-bottom: 0px;
    }

    .day-is-done__pic {
        width: 100px;
        height: 70px;
    }

    .day-is-done__pic img{
        width: 40px;
    }

    .day-is-done__pic svg {
        width: 20px;
    }
}