.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    transform: translate(-50%, -50%);
    background: white;
    padding: 50px;
    border-radius: 50px;
    max-width: 580px;
    width: calc(100% - 30px);
    outline: none;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
    backdrop-filter: blur(47px);
    -webkit-backdrop-filter: blur(47px);
}

.modal__close {
    position: absolute;
    right: 30px;
    top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.form-input {
    display: flex;
    width: 100%;
    flex-direction: column;
}

.form-input__label {
    font-size: 14px;
    font-weight: 400;
    color: #000;
    margin-bottom: 7px;
}

.input {
    height: 46px;
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #0000000A;
    padding: 0 45px;
    font-size: 14px;
    color: #000;
    border-radius: 23px;
    background-repeat: no-repeat;
    background-position: 15px center;
}

.input {}

.input_type_password {
    position: relative;
    background-image: url(../../public/assets/images/lock-icon.svg) !important;
}

.input_type_email {
    background-image: url(../../public/assets/images/email-icon.svg) !important;
}

.user-modal__title {
    margin-bottom: 60px;
}

.user-modal__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 60px;
}

.form-input {
    margin-bottom: 30px;
}

.user-modal__button {
    width: max-content;
    text-transform: uppercase;
}

.user-modal__button>span {
    display: flex;
    gap: 10px;
    align-items: center;
}

.form__forget-password {
    font-size: 11px;
    line-height: 1;
    font-weight: 400;
    color: #000;
    text-decoration: underline;
    display: inline-flex;
    margin-left: auto;
    margin-top: 5px;
}

.user-sent{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.form-inputs {
    display: flex;
    align-items: flex-start;
    gap: 20px;
}

.form__policies {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.input_error {
    border-color: #ff4444 !important;
    box-shadow: 0 0 0 1px #ff4444;
}

.checkbox_error {
    color: #ff4444;
}

.error-message {
    color: #ff4444;
    font-size: 12px;
    margin-top: 4px;
    display: block;
}


.policy-checkbox input {
    display: none;
}

.policy-checkbox label {
    font-size: 12px;
    font-weight: 400;
    color: #000;
    line-height: 1.2;
    padding-left: 30px;
    position: relative;
    display: block;
    align-items: center;
}

.policy-checkbox label a {
    color: #000;
    text-decoration: underline;
}

.policy-checkbox label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 20px;
    height: 20px;
    border: 2px solid #0000002B;
    display: flex;
    border-radius: 50%;
}

.policy-checkbox input:checked+label:before {
    background-color: var(--orange);
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px;
}

.penalty-modal {
    max-width: 960px;
}

.penalty-header {
    position: relative;
    width: 180px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
}

.game-modal-header {
    position: relative;
    /* width: 180px; */
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
    width: max-content;
    margin-top: -70px;
    transform: translateX(20px);
}

.game-modal-header__button {
    width: max-content;
}

.penalty-header__pic,
.game-modal-header__pic {
    position: absolute;
    left: -60px;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;
}

.penalty-modal__button {
    width: 300px;
    margin-top: 85px;
    margin-left: auto;
    margin-right: auto;
}

.game-modal__button {
    width: 300px;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
}

.penalty-paid {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.penalty-paid__image {
    margin-bottom: 30px;
}


@media screen and (max-width: 600px) {

    .user-modal__title {
        margin-bottom: 30px;
    }

    .penalty-header__pic,
    .game-modal-header__pic {
        width: 70px;
        left: -35px;
    }

    .user-modal__buttons {
        flex-direction: column-reverse;
        width: 100%;
        gap: 10px;
        margin-top: 40px;
    }

    .user-modal__button {
        width: 100%;
    }

    .form__policies {
        gap: 15px;
    }

    .modal {
        padding: 23px;
        transform: none;
        left: 0;
        right: 0;
        top: auto;
        margin: auto;
        bottom: 10px;
    }

    .game-modal-header {
        margin-top: -40px;
        transform: translateX(10px);
    }

    .game-modal__text{
        font-size: 18px;
    }

    .form-input {
        margin-bottom: 20px;
    }

    .form-inputs {
        flex-direction: column;
        gap: 0;
    }
}